import ButtonItems from '../../components/ButtonItems.vue';
import topBar from '../../components/TopBar.vue'
export default {
	components: {
		ButtonItems,
		topBar
	},
	data() {
		return {
			scrollLeft: 0,
			isClickChange: false,
			tabIndex: 0,
			newsitems: [],
			pageNo: 1,
			pageSize: 10,
			order: 'asc',
			sortBy: '',
			pageNoB: 1,
			pageSizeB: 10,
			orderB: 'asc',
			sortByB: '',
			loanData: [],
			borrowData: [],
			showData:[],
			tabBars: [{
				name: '借款记录',
				id: 'guanzhu'
			}, {
				name: '还款记录',
				id: 'tuijian'
			}]
		}
	},
	onLoad() {
		this.getData()
	},
	methods: {
		goPath(url){
			uni.navigateTo({url})
		},
		getData(){
			if (this.tabIndex == 0) {
				this.getLoanData()
			} else {
				this.getBorrowData()
			}
		},
		// 还款
		getBorrowData(){
			this.request({
				url: 'repayment-plans/projects/1',
				params:{
					pageNo: this.pageNoB, 
					pageSize: this.pageSizeB,
					order: this.orderB,
					sortBy: this.sortByB
				}
			}).then(res =>{
				this.borrowData = this.borrowData.concat(res.data.result)
				this.showData = this.borrowData
			}).catch(err => {
				uni.showToast({
					icon: 'none',
					title: '数据获取失败11，重新登录！'
				});
			})
		},
		// 借款
		getLoanData() {
			this.request({
				url: 'transaction/my/projects/q',
				params:{
					pageNo: this.pageNo, 
					pageSize: this.pageSize,
					order: this.order,
					sortBy: this.sortBy
				}
			}).then(res =>{
				this.loanData = this.loanData.concat(res.data.result.content)
				this.showData = this.loanData
			}).catch(err => {
				uni.showToast({
					icon: 'none',
					title: '数据获取失败22，重新登录！'
				});
			})
		},
		loadMore(e) {
			// this.addData(e);
		},
		addData(e) {
			if (this.newsitems[e].data.length > 30) {
				this.newsitems[e].loadingText = '没有更多了';
				return;
			}
			for (let i = 1; i <= 10; i++) {
				this.newsitems[e].data.push(tpl['data' + Math.floor(Math.random() * 5)]);
			}
		},
		async changeTab(e) {
			let index = e.target.current;
			if (index == 0){
				this.showData = this.loanData.length >0 ? this.loanData : this.getLoanData()
			}else{
				this.showData = this.borrowData.length >0 ? this.borrowData : this.getBorrowData()
			}
			
			let tabBar = await this.getElSize("tab-bar"),
				tabBarScrollLeft = tabBar.scrollLeft;
			let width = 0;

			for (let i = 0; i < index; i++) {
				let result = await this.getElSize(this.tabBars[i].id);
				width += result.width;
			}
			let winWidth = uni.getSystemInfoSync().windowWidth,
				nowElement = await this.getElSize(this.tabBars[index].id),
				nowWidth = nowElement.width;
			if (width + nowWidth - tabBarScrollLeft > winWidth) {
				this.scrollLeft = width + nowWidth - winWidth;
			}
			if (width < tabBarScrollLeft) {
				this.scrollLeft = width;
			}
			this.isClickChange = false;
			this.tabIndex = index; //一旦访问data就会出问题
		},
		getElSize(id) { //得到元素的size
			return new Promise((res, rej) => {
				uni.createSelectorQuery().select("#" + id).fields({
					size: true,
					scrollOffset: true
				}, (data) => {
					res(data);
				}).exec();
			})
		},
		async tapTab(e) { //点击tab-bar
			let tabIndex = e.target.dataset.current;
			if (this.tabIndex === tabIndex) {
				return false;
			} else {
				let tabBar = await this.getElSize("tab-bar"),
					tabBarScrollLeft = tabBar.scrollLeft; //点击的时候记录并设置scrollLeft
				this.scrollLeft = tabBarScrollLeft;
				this.isClickChange = true;
				this.tabIndex = tabIndex;
			}
		},
		randomfn() {
			let ary = [];
			for (let i = 0, length = this.tabBars.length; i < length; i++) {
				let aryItem = {
					loadingText: '加载更多...',
					data: []
				};
				if (i < 1) {
					for (let j = 1; j <= 10; j++) {
						aryItem.data.push(tpl['data' + Math.floor(Math.random() * 5)]);
					}
				}
				ary.push(aryItem);
			}
			return ary;
		}
	}
}
